import {Component, Injector, OnInit} from '@angular/core';
import {ThemeConstants} from '../../shared/config/theme-constant';
import 'ammap3';
import 'ammap3/ammap/maps/js/usaLow';
import 'src/assets/js/jquery.sparkline/jquery.sparkline.js';
import {BaseComponent} from '../../../@calls/components/base/base.component';

@Component({
    templateUrl: 'dashboard.html',
    styleUrls: ['./dashboard.component.scss']
})

export class DashboardComponent extends BaseComponent implements OnInit {
    departments = '_';
    calls = '_';
    companies = '_';
    users = '_';

    constructor(injector: Injector) {
        super(injector);
    }

    ngOnInit() {
        if (this.authService.currentUser.type === 'super_admin') {
            this.getSuperDetails();
        } else {
            this.getDetails();
        }
    }

    getSuperDetails() {
        this.dashboardService.getAdminDetails().subscribe((res) => {
            this.departments = res.content.data.department;
            this.calls = res.content.data.calls;
            this.companies = res.content.data.companies;
        }, (err) => {
            console.log(err);
        });
    }


    getDetails() {
        const id = this.authService.currentUser.company_id;
        this.dashboardService.getDetails(id).subscribe((res) => {
            this.departments = res.content.data.department;
            this.calls = res.content.data.calls;
            this.users = res.content.data.users;
        }, (err) => {
            console.log(err);
        });
    }
}
